@import url("./fonts.scss");
$text-color: #ffe72d;

$second-color: #81a4cd;
$third-color: #054a91;
$fourth-color: #3e7cb1;
$orange-color: #f17300;

$main-color: #1b2223;
$accent-color: #00ecc1;
$accent-color-placeholder: #00ecc1aa;
$accent-color-shadow: #00ecc155;
$white-color-shadow: #f4fefd42;
$white-color: #f4fefd;
$white-color-placeholder: #f4fefd8b;
$grey-color: #3a4f50;
$grey-placeholder-color: #496364c3;

$title-font: "Evolventa Bold", sans-serif;
$text-font: "Evolventa Regular", sans-serif;

@mixin wave-top {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100px;
  top: -40%;
  left: 0;

  &::before {
    content: "";
    position: absolute;
    z-index: 3;
    pointer-events: none;
    background-repeat: no-repeat;
    bottom: -1px;
    left: -1px;
    right: -1px;
    top: -1px;
    background-size: 100% 81px;
    background-position: 50% 100%;
    background-image: url('data:image/svg+xml;charset=utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.28 2.17" preserveAspectRatio="none"><path d="M35.28 1.67c-3.07-.55-9.27.41-16.15 0-6.87-.4-13.74-.58-19.13.1v.4h35.28z" fill="%233a4f50"/><path d="M35.28 1.16c-3.17-.8-7.3.4-10.04.56-2.76.17-9.25-1.47-12.68-1.3-3.42.16-4.64.84-7.04.86C3.12 1.31 0 .4 0 .4v1.77h35.28z" opacity=".5" fill="%233a4f50"/><path d="M35.28.31c-2.57.84-7.68.3-11.8.43-4.1.12-6.85.61-9.57.28C11.18.69 8.3-.16 5.3.02 2.3.22.57.85 0 .87v1.2h35.28z" opacity=".5" fill="%233a4f50"/></svg>');
  }
}

@mixin wave-bottom {
  overflow: hidden;
  position: absolute;
  width: 100%;
  bottom: -40%;
  height: 100px;
  left: 0;

  &::before {
    content: "";
    position: absolute;
    bottom: -1px;
    left: -1px;
    right: -1px;
    top: -1px;
    z-index: 3;
    pointer-events: none;
    background-repeat: no-repeat;
    background-size: 100% 90px;
    background-position: 50% 0%;
    background-image: url('data:image/svg+xml;charset=utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.28 2.17" preserveAspectRatio="none"><path d="M0 .5c3.07.55 9.27-.42 16.14 0 6.88.4 13.75.57 19.14-.11V0H0z" fill="%233a4f50"/><path d="M0 1c3.17.8 7.29-.38 10.04-.55 2.75-.17 9.25 1.47 12.67 1.3 3.43-.17 4.65-.84 7.05-.87 2.4-.02 5.52.88 5.52.88V0H0z" opacity=".5" fill="%233a4f50"/><path d="M0 1.85c2.56-.83 7.68-.3 11.79-.42 4.1-.12 6.86-.61 9.58-.28 2.73.33 5.61 1.17 8.61 1 3-.19 4.73-.82 5.3-.84V.1H0z" opacity=".5" fill="%233a4f50"/></svg>');
  }
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  scrollbar-width: 12px;
  scrollbar-color: $accent-color;

  /* font-family: "Evolventa Regular", sans-serif;
  letter-spacing: 2px; */
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  background: $grey-color;
  box-shadow: 5px 5px 5px -5px #223c5033 inset;
}

body::-webkit-scrollbar-thumb {
  background-color: $white-color;
  border-radius: 20px;
}

body {
  margin: 0;
  background: $main-color;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.col-2 {
  width: 16.666%;
}

.col-3 {
  width: 25%;
}

.col-4 {
  width: 33.333%;
}

.col-6 {
  width: 50%;
}

.col-8 {
  width: 66.666%;
}

.col-10 {
  width: 83.333%;
}

.col-12 {
  width: 100%;
}

.col-5 {
  width: 20%;
}

.row {
  display: flex;
}

.grid-row {
  display: grid;
}

.container {
  width: 1200px;
  margin: 0 auto;
}

h2 {
  font-size: 30px;
  margin: 0;
  text-transform: uppercase;
}

p,
h3 {
  margin: 0;
}

.header {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 4;
  width: 80%;
  transition: all 1s ease;
  background: radial-gradient(ellipse farthest-side at top left, #1b2223 0%, #3a4f50 100%);
  border-radius: 100px 25px;
  padding: 5px;

  &__nav {
    & .grid-row {
      grid-template-columns: 0.3fr 1.5fr 0.7fr;
      justify-items: center;
    }

    &--logo {
      position: relative;

      & img {
        position: relative;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 65%;
      }
    }

    &--menu {
      justify-self: end;
      font-family: $title-font;

      &--list {
        display: flex;
        justify-content: center;
        padding: 20px 0;
        font-size: 18px;

        & li {
          margin-right: 20px;

          & a {
            color: $white-color;

            &:hover {
              color: $accent-color;
              border-bottom: 2px solid $white-color;
              padding-bottom: 5px;
            }
          }
        }
      }
    }

    &--links {
      & .grid-row {
        grid-template-columns: none;
        grid-template-rows: repeat(2, 1fr);
        row-gap: 5px;
      }

      &--number {
        font-size: 22px;

        &--a {
          color: $accent-color;

          &:hover {
            color: $white-color;
          }
        }
      }

      &--socials {
        & .grid-row {
          grid-template-columns: repeat(4, 1fr);
          grid-template-rows: none;
          column-gap: 10px;
        }

        &--item {
          font-size: 25px;
          transition: all 0.5s ease;
          color: $white-color;

          &:hover {
            color: $accent-color;
            cursor: pointer;
            transform: scale(1.3);
            transition: transform 0.5s;
          }
        }
      }
    }
  }
}

.sticky {
  transition: all 1s ease-out;
  box-shadow: #00ecc166 -5px 5px, #00ecc14d -10px 10px, #00ecc133 -15px 15px;
}

.main {
  &__banner {
    min-height: 100vh;
    background: url("../images/banner/banner-7.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: $main-color;
    position: relative;

    &--title {
      text-align: center;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);

      & h1 {
        font-size: 45px;
        text-transform: uppercase;
        margin: 0;
        color: $white-color;
        font-family: $text-font;
        letter-spacing: 2px;

        & span {
          font-size: 60px;
          color: $accent-color;
          font-family: $title-font;
          letter-spacing: 2px;
        }
      }

      &--line {
        margin-top: 10px;
        display: block;
        height: 3px;
        background: $white-color;
      }

      & h2 {
        margin-top: 10px;
        color: $white-color;
        font-size: 25px;
        text-transform: initial;
        font-family: $text-font;
      }

      &--btn {
        width: 250px;
        height: 50px;
        position: relative;
        overflow: hidden;
        border: 2px solid $white-color;
        transition: color 0.5s;
        z-index: 1;
        font-size: 18px;
        border-radius: 20px;
        font-weight: bold;
        color: $accent-color;
        cursor: pointer;
        background: transparent;
        margin-top: 50px;
        font-family: $text-font;
        outline: none;

        &::before {
          content: "";
          position: absolute;
          z-index: -1;
          background-image: linear-gradient(to right bottom, #0dbb9b, #58c2ac, #82c7ba, #a7cdc6, #cad1d0);
          height: 350px;
          width: 350px;
          border-radius: 50%;
          top: 100%;
          left: 100%;
          transition: 0.7s;
        }

        &:hover {
          color: #fff;
          border: 2px solid $accent-color;

          &::before {
            top: -90%;
            left: -50px;
          }
        }
      }
    }
  }

  &__quotes {
    background: $grey-color;
    position: relative;

    &--topWave {
      @include wave-top;
    }

    &--bottomWave {
      @include wave-bottom;
    }

    &--info {
      &--slider {
        width: 800px;
        height: 220px;
        color: $white-color;

        &--slide {
          padding: 30px;

          &--header {
            display: flex;
            justify-content: space-between;

            &--line {
              margin: auto 50px;
              display: block;
              background: $white-color;
              width: 180px;
              height: 3px;
            }

            &--icon {
              display: block;
              color: $accent-color;
              font-size: 35px;
              font-weight: bold;
              text-transform: uppercase;
              font-family: $title-font;
              letter-spacing: 2px;
            }
          }

          &--desc {
            font-size: 30px;
            text-align: center;
            margin-top: 20px;
            user-select: none;
            font-family: $text-font;
          }
        }
      }
    }
  }

  &__aboutUs {
    padding: 120px 0 100px;

    &--header {
      text-align: center;
      color: $accent-color;
      letter-spacing: 5px;
      font-family: $title-font;
    }

    &--info {
      margin-top: 80px;

      & .first,
      .second {
        grid-template-columns: repeat(3, 1fr);
        justify-items: center;

        & > div {
          &:first-child {
            justify-self: end;
          }

          &:last-child {
            justify-self: start;
          }
        }
      }

      & .second {
        grid-template-columns: repeat(2, 1fr);
        margin-top: 60px;
        column-gap: 60px;
      }

      &--item {
        width: 300px;
        height: 100px;
        position: relative;
        background: $grey-color;
        display: flex;
        font-size: 15px;
        font-weight: bold;
        text-align: center;
        text-transform: uppercase;
        padding: 20px 30px 20px 25px;
        box-shadow: #00ecc166 -5px 5px, #00ecc14d -10px 10px, #00ecc133 -15px 15px;
        border-radius: 100px 25px;
        font-family: $text-font;
        letter-spacing: 2px;

        &--icon {
          position: relative;

          & > svg {
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            & > .phone-circle,
            .monitor-window,
            .key,
            .key-hole {
              transform-origin: 32px 32px;
            }
          }
        }

        &--desc {
          width: 100%;
          color: $white-color;

          & > p {
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  }

  &__visaTypes {
    padding-bottom: 100px;
    position: relative;

    &--header {
      text-align: center;
      margin-bottom: 80px;
      color: $accent-color;
      letter-spacing: 5px;
      font-family: $title-font;
    }

    .grid-row {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 50px;
      row-gap: 100px;
      justify-items: center;
    }

    &--item {
      width: 300px;
      height: 450px;
      perspective: 1000px;
      background-color: transparent;

      &--content {
        position: relative;
        width: 100%;
        height: 100%;
        transition: transform 0.8s;
        transform-style: preserve-3d;

        &--front,
        &--back {
          box-shadow: 0 8px 14px 0 #0003;
          position: absolute;
          display: flex;
          flex-direction: column;
          width: 100%;
          height: 100%;
          backface-visibility: hidden;
          border-radius: 1rem;
        }

        &--front {
          background: $grey-color;
          color: #fff;
          font-size: 18px;
          padding: 50px;
          overflow: hidden;
          cursor: default;
          font-family: $text-font;
          letter-spacing: 2px;

          &--country {
            font-size: 20px;
            z-index: 1;
            color: $accent-color;
          }

          &--title {
            text-transform: uppercase;
            font-size: 35px;
            margin: 10px 0;
            z-index: 1;
            font-family: $title-font;
            letter-spacing: 2px;
          }

          &--type {
            z-index: 1;
          }

          &--arrow {
            width: 60px;
            position: absolute;
            right: 10%;
            bottom: 5%;
          }
        }

        &--back {
          background: url("../images/ssscribble-mainColor.svg");
          background-position: center;
          background-repeat: no-repeat;
          background-size: cover;
          background-color: $grey-color;
          color: white;
          transform: rotateY(180deg);
          font-size: 18px;
          padding: 25px;
          position: relative;
          cursor: default;
          text-align: center;
          font-family: $title-font;
          letter-spacing: 1px;

          &--desc {
            position: relative;
            min-height: 220px;

            & p {
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
            }
          }

          h3 {
            font-size: 25px;
          }

          &--openningDate {
            position: absolute;
            width: 250px;
            bottom: 28%;
            left: 50%;
            transform: translateX(-50%);
            border-bottom: 2px solid $accent-color;
            padding-bottom: 5px;
            font-size: 14px;
          }

          &--moreBtn {
            position: absolute;
            bottom: 19%;
            left: 50%;
            transform: translateX(-50%);
            background: transparent;
            border: none;
            outline: none;
            cursor: pointer;
            font-size: 18px;
            font-weight: bold;
            color: #fff;

            &::after {
              content: "";
              width: 0%;
              height: 2px;
              background: $accent-color;
              display: block;
              transition: 0.3s;
              margin-top: 2px;
            }

            &:hover {
              &::after {
                width: 100%;
              }
            }
          }

          &--reqBtn {
            position: absolute;
            bottom: 5%;
            left: 50%;
            transform: translateX(-50%);
            background: $accent-color;
            width: 200px;
            height: 50px;
            cursor: pointer;
            font-size: 18px;
            border-radius: 10px;
            border: none;
            outline: none;
            color: $main-color;
            transition: 0.8s;
            font-weight: bold;

            &:hover {
              background: none;
              border: 2px solid $accent-color;
              color: $accent-color;
              box-shadow: 0 0 15px 10px $accent-color-shadow;
            }
          }
        }
      }

      &:nth-child(odd) &--content--front {
        &::before {
          content: "";
          width: 200px;
          height: 300px;
          background-image: linear-gradient(to right bottom, #1b2223, #222d2e, #2a3839, #324344, #3a4f50);
          position: absolute;
          left: -25%;
          top: 50%;
          border-radius: 20px;
          transform: rotate(45deg);
          z-index: 0;
        }
      }

      &:nth-child(even) &--content--front {
        &::before {
          content: "";
          width: 200px;
          height: 200px;
          background-image: linear-gradient(to right bottom, #1b2223, #222d2e, #2a3839, #324344, #3a4f50);
          position: absolute;
          right: -20%;
          top: -5%;
          z-index: 0;
          border-radius: 50%;
        }
      }

      &:hover &--content {
        transform: rotateY(180deg);
      }
    }

    &--moreInfo {
      position: fixed;
      inset: 0;
      background: #0008;
      display: none;
      cursor: pointer;
      z-index: 100;

      &--modal {
        position: relative;
        max-width: 800px;
        background: url("../images/banner/banner-lines.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        margin: auto;
        border-radius: 20px;
        padding: 30px;
        box-shadow: 0 0 15px 0 $accent-color-shadow;
        cursor: default;
        font-family: "Evolventa Regular", sans-serif;
        letter-spacing: 1px;

        .grid-row {
          grid-template-columns: repeat(2, 1fr);
          column-gap: 50px;
          row-gap: 100px;
          justify-items: center;
        }

        &--desc {
          &--header {
            color: $accent-color;
            margin-bottom: 20px;
            text-align: center;
            font-family: $title-font;

            &--line {
              display: block;
              width: 100%;
              height: 2px;
              background: $white-color;
              margin: 5px 0;
            }

            & h3 {
              font-size: 20px;
            }
          }

          &--leftInfo {
            color: $white-color;
            text-align: justify;
            font-size: 18px;
            letter-spacing: 2px;

            & p {
              margin-top: 10px;
            }

            & ol {
              padding-left: 18px;
              margin: 10px 0 0 5px;
              text-align: left;

              & li {
                &::marker {
                  color: $accent-color;
                }
              }
            }
          }

          &--rightInfo {
            color: $white-color;
            font-size: 18px;

            & ol {
              padding-left: 18px;
              margin: 10px 0 0 5px;

              & li {
                &::marker {
                  color: $accent-color;
                }
              }
            }
          }

          &--opening {
            position: absolute;
            bottom: 40px;
            left: 5%;
            text-align: center;
            font-size: 20px;
            color: $accent-color;
            font-family: $title-font;

            &--span {
              color: $white-color;
            }
          }
        }

        &--reqBtn {
          position: relative;
          left: 100%;
          transform: translateX(-100%);
          background: $accent-color;
          width: 200px;
          height: 50px;
          cursor: pointer;
          font-size: 18px;
          border-radius: 10px;
          border: none;
          outline: none;
          color: $main-color;
          transition: 0.8s;
          font-weight: bold;
          margin-top: 20px;

          &:hover {
            background: none;
            border: 2px solid $accent-color;
            color: $accent-color;
            box-shadow: 0 0 15px 10px $accent-color-shadow;
          }
        }

        &--closeBtn {
          position: absolute;
          top: 10px;
          right: 10px;
          cursor: pointer;
          background: none;
          border: none;
          outline: none;
          font-size: 20px;
          color: $white-color;

          &:hover {
            color: $accent-color;
          }
        }
      }
    }
  }

  &__consultation {
    padding: 50px 0;
    background: $accent-color;
    position: relative;
    font-family: $text-font;

    &--topWave {
      @include wave-top;

      top: -21%;

      &::before {
        background-image: url('data:image/svg+xml;charset=utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.28 2.17" preserveAspectRatio="none"><path d="M35.28 1.67c-3.07-.55-9.27.41-16.15 0-6.87-.4-13.74-.58-19.13.1v.4h35.28z" fill="%23f17300"/><path d="M35.28 1.16c-3.17-.8-7.3.4-10.04.56-2.76.17-9.25-1.47-12.68-1.3-3.42.16-4.64.84-7.04.86C3.12 1.31 0 .4 0 .4v1.77h35.28z" opacity=".5" fill="%23f17300"/><path d="M35.28.31c-2.57.84-7.68.3-11.8.43-4.1.12-6.85.61-9.57.28C11.18.69 8.3-.16 5.3.02 2.3.22.57.85 0 .87v1.2h35.28z" opacity=".5" fill="%23f17300"/></svg>');
      }
    }

    &--bottomWave {
      @include wave-bottom;

      bottom: -21%;

      &::before {
        background-image: url('data:image/svg+xml;charset=utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.28 2.17" preserveAspectRatio="none"><path d="M0 .5c3.07.55 9.27-.42 16.14 0 6.88.4 13.75.57 19.14-.11V0H0z" fill="%23f6b981"/><path d="M0 1c3.17.8 7.29-.38 10.04-.55 2.75-.17 9.25 1.47 12.67 1.3 3.43-.17 4.65-.84 7.05-.87 2.4-.02 5.52.88 5.52.88V0H0z" opacity=".5" fill="%23f6b981"/><path d="M0 1.85c2.56-.83 7.68-.3 11.79-.42 4.1-.12 6.86-.61 9.58-.28 2.73.33 5.61 1.17 8.61 1 3-.19 4.73-.82 5.3-.84V.1H0z" opacity=".5" fill="%23f6b981"/></svg>');
      }
    }

    .grid-row {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 100px;
    }

    &--info {
      padding: 20px 0 0;
      text-align: center;

      & h2 {
        font-size: 45px;
        color: $main-color;
        letter-spacing: 2px;
        font-family: $title-font;
      }

      & p {
        font-size: 25px;
        margin-top: 10px;
        color: $grey-color;
      }
    }

    &--form {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 80%;
      height: 350px;
      flex-direction: column;
      gap: 35px;
      background: url("../images/ssscribble-mainColor.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      background-color: $grey-color;
      box-shadow: #3a4f5066 -5px 5px, #3a4f504d -10px 10px, #3a4f5033 -15px 15px;
      border-radius: 8px;
      position: relative;

      &--name,
      &--number,
      &--visaType {
        position: relative;
        width: 300px;

        &--input {
          width: 100%;
          height: 40px;
          font-size: 20px;
          border-radius: 10px;
          border: none;
          border-left: 2px solid $accent-color-shadow;
          border-bottom: 2px solid $accent-color-shadow;
          background: transparent;
          outline: none;
          color: $white-color;
          padding-left: 10px;
          cursor: pointer;
          font-family: $text-font;
          letter-spacing: 2px;

          &::placeholder {
