          & .grid-row {
            grid-template-columns: 155px 155px;
            justify-items: center;
          }

          &--step {
            height: 200px;
            width: 155px;
            position: relative;

            &--anotherWrap {
              width: 100%;
              height: 100%;
              overflow: hidden;
              position: relative;

              &--span {
                position: absolute;
                top: 10%;
                left: -25%;
                width: 160px;
                height: 160px;
                background: $accent-color;
                border-radius: 50%;
                box-shadow: 12px 10px 14px -13px #000000ba;
              }
            }

            &--number {
              position: absolute;
              left: -25%;
              top: 18%;
              width: 130px;
              height: 130px;
              background: $white-color;
              border-radius: 50%;
              box-shadow: 0 0 20px 5px #223c5033 inset;

              &--one {
                font-size: 80px;
                font-weight: bold;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                color: $grey-color;
              }
            }
          }

          &--header {
            padding: 20px 35px 0 0;

            &--icon {
              text-align: center;
            }

            &--text {
              font-size: 20px;
              text-align: center;
              margin-top: 12px;
              color: $accent-color;
              line-height: 24px;
              font-family: $title-font;
            }

            &--circle {
              width: 20px;
              height: 20px;
              position: absolute;
              right: 8%;
              top: 28%;
              background: $accent-color;
              border-radius: 50%;
            }
          }

          &--info {
            text-align: center;
            font-size: 16px;
            height: 50%;
            position: relative;

            & p {
              position: absolute;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
              width: 80%;
            }
          }

          &--lines {
            height: 4px;
            display: flex;
            margin-top: 14px;
            position: absolute;
            top: 45%;
            left: 44%;

            &--span {
              display: block;
              background: $white-color;
              width: 26px;
              height: 2px;
              margin-left: 20px;
            }
          }

          &--circle--bottom {
            width: 40px;
            height: 40px;
            position: absolute;
            background: $accent-color;
            border-radius: 50%;
            bottom: -5%;
            left: 30%;
          }

          &--circle--left {
            width: 25px;
            height: 25px;
            position: absolute;
            background: $white-color;
            border-radius: 50%;
            top: 0;
            left: -20%;
            box-shadow: 0 0 20px 5px #223c5033 inset;
            animation: circle 6s linear infinite;
          }

          &--plus {
            position: absolute;
            right: 17%;
            bottom: 12%;

            &--first,
            &--second {
              display: block;
              width: 15px;
              height: 2px;
              background: $main-color;
            }

            &--second {
              transform: rotate(90deg) translate(-2px, 0);
            }
          }

          &--emptyCircle {
            display: block;
            width: 20px;
            height: 20px;
            position: absolute;
            left: 6%;
            top: 53%;
            border: 3px solid $main-color;
            border-radius: 50%;
          }

          @keyframes circle {
            0% {
              transform: rotate(0deg) translate(-5px) rotate(0deg);
            }

            100% {
              transform: rotate(360deg) translate(-5px) rotate(-360deg);
            }
          }

          @keyframes slide-top {
            0% {
              transform: translateY(0);
            }

            100% {
              transform: translateY(-20px);
            }
          }
        }
      }
    }
  }

  &__news {
    padding: 60px 0;

    &--header {
      text-align: center;
      color: $accent-color;
      letter-spacing: 5px;
      font-family: $title-font;
    }

    &--info {
      margin-top: 80px;
